@charset "utf-8";

.table-common {
  table,
  thead,
  tbody,
  tr,
  th,
  td {
    border: none;
    outline: none;
    font-weight: $fontweight-default;
    letter-spacing: $letter-spacing-title;
  }

  .table-wrapper {
    min-width: 9.2rem;
  }

  .content-list {
    @include reset-list;
  }

  .heading-title {
    background-color: $color-background-title;
    color: #fff20e;
    font-size: 0.21rem;
    font-weight: $fontweight-bold;
    line-height: calc(34 / 21);
    text-align: center;
  }

  .content-title {
    color: $color-background-title;
    font-feature-settings: "palt";
    font-size: 0.21rem;
    line-height: calc(29 / 21);
  }

  .content-detail {
    color: $color-dark;
    font-size: $fontsize-md;
    line-height: calc(32 / 18);
  }

  .content-wrapper {
    &:nth-child(even) {
      background-color: $color-gray;
    }
  }

  .content-inner {
    min-height: 1rem;
  }

  @include desktop {
    .heading-title {
      padding: 16px;

      &:first-child {
        width: 404px;

        html.is-tablet & {
          width: 390px;
        }
      }
    }

    .content-title {
      padding: 22px 52px;

      html.is-tablet & {
        padding: 22px 40px;
      }
    }

    .content-detail {
      padding: 22px 20px;
    }

    .content-wrapper {
      height: 100px;
    }
  }

  @include mobile {
    .heading-title {
      padding: 0.15rem;
    }

    .content-title,
    .content-detail {
      padding: 0.1rem 0.2rem;
    }

    .content-wrapper {
      height: 1rem;
    }
  }
}
