@charset "utf-8";

.block-common-product {
  .content-detail {
    letter-spacing: $letter-spacing-default;

    .detail-text {
      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  .product-content {
    position: relative;
  }

  @include desktop {
    display: flex;

    .product-image {
      flex: 0 0 auto;
      width: 100%;
      max-width: 596px;
      margin-right: 58px;
    }

    .image-block {
      position: relative;
      padding-top: calc(430 / 596 * 100%);

      img {
        position: absolute;
        top: 0;
        left: 0;
      }
    }

    .content-detail {
      margin-bottom: 132px;
      font-size: $fontsize-title;
      line-height: calc(29 / 17);
    }

    .title-common-primary {
      margin-bottom: 83px;
    }

    .title-common-secondary {
      margin-bottom: 5px;
    }
  }

  @include mobile {
    .product-image {
      margin-bottom: 0.6rem;
    }

    .content-detail {
      margin-bottom: 0.6rem;
    }

    .title-common-secondary {
      margin-bottom: 0.1rem;
    }
  }
}

.block-common-product.is-revert {
  @include desktop {
    flex-direction: row-reverse;

    .product-image {
      margin-right: unset;
      margin-left: 45px;
    }
  }
}
