@charset "utf-8";

.accordion-common-faq {
  .accordion-wrapper {
    @include reset-list;
  }

  .accordion-questions,
  .accordion-answers {
    display: flex;
    align-items: flex-start;
  }

  .accordion-questions {
    position: relative;
    cursor: pointer;

    &::before,
    &::after {
      content: "";
      position: absolute;
      transition: transform 0.3s ease;
      background-color: $color-background-title;
    }

    &::after {
      transform: rotate(90deg);
    }

    &.is-active {
      &::after {
        transform: rotate(0);
      }
    }
  }

  .accordion-answers {
    background-color: #f9fae9;
  }

  .content-text {
    .content-link.is-block {
      display: inline-block;
    }
  }

  .content-link {
    transition: text-decoration 0.3s ease;
    text-decoration: underline;
    text-decoration-thickness: 0.01rem;
    text-underline-offset: 0.04rem;

    &:hover {
      text-decoration-color: transparent;
    }

    html.is-touchable & {
      &:hover {
        text-decoration-color: inherit;
      }
    }
  }

  .question-title {
    color: $color-background-title;
    letter-spacing: $letter-spacing-paragraph;
    font-weight: $fontweight-bold;
  }

  .answers-content {
    color: $color-dark;
    font-weight: $fontweight-default;
  }

  .content-text {
    margin-bottom: 0.2rem;

    &:last-child {
      margin-bottom: 0;
    }

    .content-note {
      display: flex;
    }
  }

  .accordion-inner {
    border-top: 1px solid #8a8a8a;

    &:last-child {
      border-bottom: 1px solid #8a8a8a;
    }
  }

  .question-icon,
  .answers-icon {
    flex: 0 0 auto;
  }

  .question-title,
  .answers-content {
    flex: 1;
  }

  @include desktop {
    .accordion-questions {
      min-height: 108px;
      padding: 32px 82px 28px 28px;

      &::before,
      &::after {
        top: 53px;
        right: 30px;
        width: 29px;
        height: 5px;
      }
    }

    .question-icon {
      margin-right: 33px;
    }

    .question-block {
      width: 39px;
    }

    .answers-block {
      width: 41px;
    }

    .question-title {
      padding-top: 3px;
      font-size: 0.25rem;
    }

    .accordion-answers {
      padding: 51px 48px 34px 28px;
    }

    .content-text {
      font-size: $fontsize-lg;
      line-height: calc(41 / 20);
    }

    .answers-content {
      margin-top: -10px;
    }

    .answers-icon {
      margin-right: 35px;
    }
  }

  @include mobile {
    .accordion-questions {
      padding: 0.2rem 0.38rem 0.2rem 0.2rem;

      &::before,
      &::after {
        top: 0.36rem;
        right: 0.05rem;
        width: 0.24rem;
        height: 0.04rem;
      }
    }

    .question-icon,
    .answers-icon {
      margin-right: 0.2rem;
    }

    .question-title {
      padding-top: 0.02rem;
      font-size: $fontsize-lg;
    }

    .question-block {
      width: 0.29rem;
    }

    .answers-block {
      width: 0.31rem;
    }

    .accordion-answers {
      padding: 0.3rem 0.2rem;
    }

    .content-text {
      font-size: $fontsize-md;
      line-height: calc(32 / 18);
    }

    .answers-content {
      margin-top: -0.06rem;
    }
  }
}
