@charset "utf-8";

.button-common-primary {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 0.5rem;
  border-radius: 0.25rem;
  background-color: $color-background-title;

  @include reset-text;

  .button-wrapper {
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    color: $color-light;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      background: url("../img/common/icon_arrow_02.png") 50% 50% / 100% auto no-repeat;
    }
  }

  .button-text {
    position: relative;
  }

  @include desktop {
    width: 250px;
    font-size: $fontsize-md;

    .button-wrapper {
      padding: 11px 40px 8px;

      &::before {
        right: 14px;
        width: 22px;
      }
    }
  }

  @include mobile {
    width: 100%;
    max-width: 3rem;
    margin-right: auto;
    margin-left: auto;
    font-size: $fontsize-lg;

    .button-wrapper {
      min-height: 0.6rem;
      padding: 0.11rem 0.5rem 0.08rem;

      &::before {
        right: 0.14rem;
        width: 0.22rem;
      }
    }
  }
}

.button-common-secondary {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 1.79rem;
  margin: 0 auto;
  overflow: hidden;
  border: 1px solid $color-background-title;
  border-radius: 0.25rem;
  color: $color-background-title;

  @include reset-text;

  .button-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    min-height: 0.5rem;
    padding: 0.1rem 0.2rem;
    text-align: center;
  }
}
