@charset "utf-8";

@mixin desktop {
  @media print, (min-width: 768px) {
    @content;
  }
}

@mixin laptop-md {
  @media (min-width: 768px) and (max-width: 1399.98px) {
    @content;
  }
}

@mixin mobile {
  @media only screen and (max-width: 767.98px) {
    @content;
  }
}

@mixin print {
  @media print {
    @content;
  }
}

@mixin scroll-hidden {
  &::-webkit-scrollbar {
    width: 0;
    opacity: 0;
  }
}

@mixin reset-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin reset-text {
  font-weight: $fontweight-semibold;

  html.is-android & {
    font-weight: $fontweight-bold;
  }
}
