@charset "utf-8";

.title-common-primary {
  font-weight: $fontweight-bold;
  letter-spacing: 0.02em;
  text-align: center;
  font-feature-settings: "palt";

  .title-wrapper {
    display: inline;
    position: relative;
    text-decoration: underline;
    text-decoration-color: $color-background-title;
    text-decoration-thickness: 0.05rem;
  }

  @include desktop {
    font-size: 0.36rem;
    line-height: calc(76 / 36);

    .title-wrapper {
      text-underline-offset: 0.2rem;
    }
  }

  @include mobile {
    margin-bottom: 0.6rem;
    font-size: 0.3rem;
    line-height: calc(54 / 30);

    .title-wrapper {
      text-underline-offset: 0.1rem;
    }
  }
}

.title-common-secondary {
  color: $color-background-title;
  font-weight: $fontweight-bold;
  letter-spacing: $letter-spacing-reset;

  @include desktop {
    font-size: 0.27rem;
  }

  @include mobile {
    font-size: 0.26rem;
  }
}

.title-common-secondary.is-large {
  text-align: center;

  @include desktop {
    font-size: 0.36rem;
  }

  @include mobile {
    font-size: 0.27rem;
  }
}

.title-common-product {
  color: $color-background-title;
  font-weight: $fontweight-bold;
  letter-spacing: $letter-spacing-reset;

  @include desktop {
    font-size: 0.23rem;
  }

  @include mobile {
    font-size: 0.22rem;
  }
}

.title-common-product.is-large {
  letter-spacing: 0.07em;
  font-feature-settings: "palt";

  @include desktop {
    font-size: 0.33rem;
  }

  @include mobile {
    margin-bottom: 0.4rem;
    font-size: 0.28rem;
  }
}
