@charset "utf-8";

.section-merit-partnership {
  @include desktop {
    padding: 115px 0 143px;

    .title-common-primary {
      margin-bottom: 129px;
    }
  }

  @include mobile {
    padding: 0.4rem 0 1rem;

    .title-common-primary {
      margin-bottom: 1rem;
    }
  }
}

.section-merit-types {
  background-color: $color-gray;

  .types-wrapper {
    overflow: hidden;
    border-radius: 0.05rem;
    background-color: $color-light;
  }

  @include desktop {
    padding: 100px 0 145px;

    .wrapper {
      max-width: 1075px;
      padding-top: 10px;
    }

    .types-wrapper {
      padding: 98px;
    }

    .menu-merit-types {
      max-width: 743px;
      margin: 0 auto;
    }

    .title-common-primary {
      margin-bottom: 65px;
    }
  }

  @include mobile {
    padding: 0.8rem 0 1rem;

    .types-wrapper {
      margin: 0 0.2rem;
      padding: 0.4rem 0.3rem;
    }
  }
}

.list-merit-partnership {
  .list-wrapper {
    @include reset-list;
  }

  .item-wrapper {
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: space-between;
    border: 0.03rem solid $color-background-title;
    border-radius: 31px;
  }

  .item-title {
    margin-bottom: 0.05rem;
    color: $color-background-title;
    font-weight: $fontweight-bold;
    letter-spacing: $letter-spacing-title;
  }

  .item-number {
    display: flex;
    position: absolute;
    top: -0.44rem;
    left: 0.25rem;
    align-items: center;
    justify-content: center;
    width: 0.79rem;
    height: 0.91rem;
    background: url("../img/merit/img_partnership_01.svg") 50% 50% / 100% 100% no-repeat;

    .number-wrapper {
      display: flex;
      position: relative;
      top: -0.05rem;
      left: 0.01rem;
      align-items: center;
      justify-content: center;
      padding: 0.05rem;
      color: #fff20e;
      font-size: 0.58rem;
      font-weight: $fontweight-bold;
      line-height: 1;
    }
  }

  .list-item {
    font-weight: $fontweight-bold;
  }

  .item-detail {
    margin-bottom: 0.3rem;
    font-weight: $fontweight-default;
    letter-spacing: $letter-spacing-paragraph;
    font-feature-settings: "palt";

    .detail-text {
      margin-bottom: 0.32rem;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  @include desktop {
    .list-wrapper {
      display: flex;
      flex-wrap: wrap;
      margin: -36px;
    }

    .list-item {
      width: calc(100% / 2);
      padding: 36px;
    }

    .item-wrapper {
      min-height: 642px;
      padding: 52px 47px 33px;
    }

    .list-item.is-third {
      .item-wrapper {
        padding: 52px 47px;
      }
    }

    .item-title {
      font-size: 0.41rem;
    }

    .item-detail {
      font-size: $fontsize-title;
      line-height: calc(32 / 17);
    }
  }

  @include mobile {
    .item-wrapper {
      min-height: 5rem;
      padding: 0.5rem 0.4rem;
    }

    .item-title {
      font-size: 0.34rem;
    }

    .list-item {
      margin-bottom: 1rem;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

.list-merit-options {
  .list-child {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 0 -0.14rem;
    padding: 0;
    list-style: none;
  }

  .item-describe {
    font-feature-settings: "palt";
  }

  .item-inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;

    .item-describe {
      color: $color-background-title;
      font-size: 0.15rem;
      letter-spacing: $letter-spacing-title;
      line-height: calc(26 / 15);
    }

    .item-describe {
      &.is-underline {
        transition: text-decoration 0.3s ease;
        text-decoration: underline;
        text-decoration-thickness: 0.01rem;
        text-underline-offset: 0.04rem;
      }
    }
  }

  a.item-inner {
    &:hover {
      .item-describe {
        text-decoration-color: transparent;
      }
    }

    html.is-touchable & {
      &:hover {
        .item-describe {
          text-decoration-color: inherit;
        }
      }
    }
  }

  .item-image {
    width: 1.61rem;
    height: 1.61rem;
    margin-bottom: 0.13rem;
    overflow: hidden;
    border-radius: 50%;
  }

  .child-item {
    width: calc(100% / 2);
    padding: 0 0.14rem;
  }

  @include mobile {
    .list-child {
      margin: -0.2rem;
    }

    .child-item {
      padding: 0.2rem;
    }
  }
}

.menu-merit-types {
  .menu-wrapper {
    padding: 0;
    list-style: none;
  }

  .item-title {
    margin-bottom: 0.15rem;
    color: $color-background-title;
    font-weight: $fontweight-bold;
    letter-spacing: $letter-spacing-paragraph;
  }

  @include desktop {
    .menu-wrapper {
      display: flex;
      flex-wrap: wrap;
    }

    .menu-item {
      min-width: 286px;
      padding-right: 20px;

      &:last-child {
        min-width: calc(100% - 286px * 2);
        margin-right: -30px;
        margin-left: 10px;
      }
    }

    .item-title {
      font-size: 0.24rem;
    }
  }

  @include mobile {
    .menu-wrapper {
      margin: 0;
    }

    .menu-item {
      margin-bottom: 0.5rem;

      &:last-child {
        margin-bottom: 0;
      }
    }

    .item-title {
      font-size: 0.28rem;
    }
  }
}
