@charset "utf-8";

.logo-common-primary {
  .logo-wrapper {
    display: block;
  }

  .logo-describe {
    display: inline-block;
    font-weight: $fontweight-bold;
    letter-spacing: $letter-spacing-paragraph;
  }

  @include desktop {
    .logo-wrapper {
      width: 290px;
    }

    .logo-describe {
      margin-bottom: 5px;
      font-size: $fontsize-paragraph;
    }
  }

  @include mobile {
    .logo-wrapper {
      width: 2.38rem;
    }

    .logo-describe {
      margin-bottom: 0.03rem;
      font-size: 0.13rem;
      line-height: $line-height-title;
    }
  }
}

.logo-common-primary.is-footer {
  @include desktop {
    margin-bottom: 46px;
  }

  @include mobile {
    margin-bottom: 0.3rem;
  }
}
