@charset "utf-8";

.title-service-information {
  color: $color-background-title;
  font-size: 0.23rem;
  letter-spacing: $letter-spacing-title;
  line-height: calc(42 / 23);

  .title-wrapper {
    display: inline-block;
    background: $color-light;
    line-height: calc(34 / 23);
  }

  @include desktop {
    margin-bottom: 9px;

    .title-wrapper {
      padding: 2px 11px 0;
    }
  }

  @include mobile {
    margin-bottom: 0.1rem;

    .title-wrapper {
      padding: 0.02rem 0.1rem 0;
    }
  }
}

.section-service-introduction {
  font-feature-settings: "palt";

  .introduction-title {
    color: $color-background-title;
    font-weight: $fontweight-bold;
    text-align: center;
  }

  .introduction-text {
    margin: 0 auto;
    letter-spacing: 0.13em;

    p {
      margin: 0;
    }
  }

  @include desktop {
    margin-top: 100px;
    padding: 15px 0 132px;

    .title-common-primary {
      margin-bottom: 104px;
    }

    .introduction-title {
      margin-bottom: 30px;
      font-size: 0.33rem;
      line-height: 1.5;
    }

    .introduction-text {
      max-width: 686px;
      margin-bottom: 90px;
      font-size: $fontsize-md;
      line-height: calc(33 / 18);

      p {
        margin-right: -20px;
      }
    }
  }

  @include mobile {
    padding: 0.4rem 0 1rem;

    .introduction-title {
      margin-bottom: 0.2rem;
      font-size: 0.3rem;
      line-height: 1.5;
    }

    .introduction-text {
      margin-bottom: 0.3rem;
    }
  }
}

.list-service-introduction {
  .list-wrapper,
  .item-list {
    @include reset-list;
  }

  .item-wrapper {
    background-color: $color-gray;
  }

  .item-image {
    margin: 0 auto;
  }

  .item-title {
    position: relative;
    color: $color-background-title;
    font-weight: $fontweight-bold;
    letter-spacing: $letter-spacing-title;

    &::before,
    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      height: 3px;
    }

    &::before {
      width: 100%;
      background-color: #fff20e;
    }

    &::after {
      z-index: 1;
      width: calc(180 / 364 * 100%);
      background-color: $color-background-title;
    }
  }

  .item-list {
    & > li {
      position: relative;
      letter-spacing: $letter-spacing-paragraph;
      font-feature-settings: normal;

      &::before {
        content: "";
        position: absolute;
        left: 0;
        border-radius: 50%;
        background-color: $color-background-title;
      }
    }
  }

  @include desktop {
    .list-wrapper {
      display: flex;
      flex-wrap: wrap;
      margin: 0 -30px;
    }

    .list-item {
      width: calc(100% / 2);
      margin-bottom: 15px;
      padding: 0 30px;
    }

    .item-wrapper {
      padding: 60px 56px 38px;
      border-radius: 20px;
    }

    .item-image {
      margin-bottom: 38px;
    }

    .item-title {
      margin-bottom: 28px;
      padding-bottom: 26px;
      font-size: 0.29rem;
      line-height: 1.4;
    }

    .item-list {
      & > li {
        padding-left: 22px;
        font-size: $fontsize-title;
        line-height: calc(32 / 16.5);

        &::before {
          top: 11px;
          width: 10px;
          height: 10px;
          margin-left: 3px;
        }
      }
    }
  }

  @include mobile {
    .list-item {
      margin-bottom: 0.3rem;
    }

    .item-wrapper {
      padding: 0.42rem;
      border-radius: 0.2rem;
    }

    .item-image {
      width: 3.68rem;
      margin: 0 auto 0.2rem;
    }

    .item-title {
      width: 3.68rem;
      margin: 0 auto 0.2rem;
      padding-bottom: 0.2rem;
      font-size: 0.26rem;
      line-height: 1.4;
    }

    .item-list {
      & > li {
        padding-left: 0.22rem;

        &::before {
          top: 0.18rem;
          width: 0.08rem;
          height: 0.08rem;
          margin-left: 0.03rem;
        }
      }
    }
  }
}

.section-service-information {
  background-color: $color-gray;

  @include desktop {
    padding: 111px 0 200px;

    .title-common-primary {
      margin-bottom: 110px;
      padding-top: 20px;
    }
  }

  @include mobile {
    padding: 0.4rem 0 1rem;
  }
}

.list-service-information {
  .list-wrapper {
    @include reset-list;
  }

  .list-item {
    &:last-child {
      margin-bottom: 0;
    }
  }

  .item-image {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      z-index: 0;
      width: 100%;
      height: 100%;
      background-color: #cfdce9;
    }

    img {
      position: relative;
      z-index: 1;
    }
  }

  .content-paragraph {
    letter-spacing: $letter-spacing-reset;

    p {
      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  @include desktop {
    .list-item {
      margin-bottom: 182px;
    }

    .item-image {
      flex: 0 0 auto;
      width: 459px;
      height: 286px;
      margin-left: 51px;

      &::before {
        top: 15px;
        left: -15px;
      }
    }

    .item-content {
      margin-top: -3px;
      padding: 0 50px;
    }

    .content-paragraph {
      font-size: $fontsize-title;
      line-height: calc(32 / 16.5);

      p {
        margin-bottom: 30px;
      }
    }

    .item-wrapper {
      display: flex;

      &.is-reserve {
        flex-direction: row-reverse;

        .item-image {
          margin-right: 52px;
          margin-left: 0;

          &::before {
            right: -18px;
            left: unset;
          }
        }

        .item-content {
          padding-right: 30px;
        }
      }

      & + .item-wrapper {
        margin-top: 198px;
        padding-top: 40px;
      }
    }
  }

  @include mobile {
    .list-item {
      margin-bottom: 1rem;
    }

    .item-image {
      margin-bottom: 0.3rem;

      &::before {
        top: 0.15rem;
        left: -0.15rem;
      }
    }

    .content-paragraph {
      p {
        margin-bottom: 0.15rem;
      }
    }

    .item-wrapper {
      &.is-reserve {
        .item-image {
          &::before {
            right: -0.18rem;
            left: unset;
          }
        }
      }

      & + .item-wrapper {
        padding-top: 1.1rem;
      }
    }
  }
}
