@charset "utf-8";

.section-case-introduction {
  @include desktop {
    padding: 115px 0 203px;

    .title-common-primary {
      margin-bottom: 98px;
    }
  }

  @include mobile {
    padding: 0.4rem 0 1rem;
  }
}

.list-case-information {
  .list-wrapper {
    @include reset-list;
  }

  .list-item {
    &:last-child {
      margin-bottom: 0;
    }
  }

  .item-title {
    color: $color-background-title;
    font-weight: $fontweight-bold;
    letter-spacing: $letter-spacing-title;
    line-height: 1.5;
  }

  .heading-text {
    position: relative;
    margin: 0;
    color: $color-background-title;
    font-weight: $fontweight-bold;
  }

  .item-heading {
    display: flex;
    align-items: flex-start;
    font-feature-settings: "palt";

    &.is-modify {
      flex-wrap: wrap;
    }

    .heading-text {
      width: 100%;
    }
  }

  .heading-label {
    display: flex;
    flex: 0 0 auto;
    align-items: center;
    justify-content: center;
    background-color: $color-background-title;
    color: $color-light;
    font-weight: $fontweight-bold;
    letter-spacing: $letter-spacing-title;
  }

  .content-paragraph {
    letter-spacing: $letter-spacing-reset;
    font-feature-settings: "palt";

    p {
      margin: 0;
    }

    ul {
      @include reset-list;
    }

    span {
      display: block;
    }
  }

  @include desktop {
    margin: 0 51px 40px;

    .list-item {
      margin-bottom: 206px;
    }

    .item-title {
      margin: 0 0 20px;
      font-size: 0.33rem;

      &.is-spacing {
        margin-right: -23px;
      }
    }

    .heading-label {
      min-height: 34px;
      margin-right: 18px;
      padding: 2px 15px 1px;
      border-radius: 20px;
      font-size: $fontsize-lg;
      line-height: 1.4;
    }

    .item-heading {
      margin-bottom: 37px;
    }

    .item-heading.is-modify {
      .heading-label {
        margin-bottom: 10px;
      }
    }

    .heading-text {
      top: 1px;
      font-size: 0.23rem;
      line-height: calc(33 / 23);
    }

    .item-inner {
      display: flex;
      align-items: flex-start;
    }

    .item-image {
      flex: 0 0 auto;
      width: 459px;
      height: 286px;
    }

    .item-content {
      flex: 1;
      margin-top: -7px;
      padding-left: 47px;
    }

    .content-paragraph {
      font-size: $fontsize-title;
      line-height: calc(32 / 16.5);

      p + p {
        margin-top: 32px;
      }

      ul {
        line-height: calc(26 / 16.5);
      }

      li {
        margin-bottom: 5px;
      }

      span {
        margin-top: -5px;
        font-size: 0.12rem;
        line-height: calc(32 / 12);
      }
    }
  }

  @include mobile {
    margin-bottom: 0.5rem;

    .list-item {
      margin-bottom: 1rem;
    }

    .item-title {
      margin: 0 0 0.12rem;
      font-size: 0.26rem;
    }

    .item-heading {
      flex-wrap: wrap;
      margin-bottom: 0.2rem;
    }

    .heading-label {
      min-height: 0.34rem;
      margin-bottom: 0.1rem;
      padding: 0.02rem 0.15rem 0.01rem;
      border-radius: 0.2rem;
      font-size: $fontsize-md;
      line-height: 1.4;
    }

    .heading-text {
      top: 1px;
      width: 100%;
      font-size: 0.22rem;
      line-height: calc(33 / 22);
    }

    .item-image {
      margin-bottom: 0.12rem;
    }

    .content-paragraph {
      font-size: $fontsize-default-sp;
      line-height: $line-height-sp;

      p + p {
        margin-top: 0.15rem;
      }

      li {
        margin-bottom: 0.05rem;
      }

      span {
        font-size: 0.13rem;
        line-height: calc(32 / 13);
      }
    }
  }
}

