.section-case-information {
  background-color: $color-gray;
  font-feature-settings: "palt";

  .information-text {
    margin: 0;
    color: $color-background-title;
    letter-spacing: 0.05em;
    text-decoration: underline;
    text-decoration-color: #fff20e;
    text-decoration-thickness: 0.02rem;
    text-underline-offset: 0.05rem;
  }

  @include desktop {
    padding: 210px 0 145px;

    .title-common-primary {
      margin-bottom: 80px;
    }

    .paragraph-common {
      max-width: unset;
      margin-right: -20px;
      margin-bottom: 211px;
    }

    .information-text {
      margin: 0 20px 0 50px;
      font-size: $fontsize-lg;
      line-height: calc(45.95 / 20);
    }
  }

  @include print {
    padding-bottom: 0;

    .information-text {
      padding-bottom: 1rem;
    }
  }

  @include mobile {
    padding: 0.4rem 0 1rem;
  }
}
