@charset "utf-8";

.section-product {
  @include desktop {
    padding: 115px 0 145px;
  }

  @include mobile {
    padding: 0.4rem 0 1rem;
  }
}

.section-product-introduction {
  .introduction-content {
    color: $color-dark;
    letter-spacing: $letter-spacing-reset;
  }

  .introduction-wrapper {
    .title-common-product {
      text-align: center;
    }
  }

  @include desktop {
    margin: 115px 0 230px;

    .wrapper {
      max-width: 930px;
    }

    .introduction-inner {
      display: flex;
    }

    .introduction-banner {
      flex: 0 0 auto;
      width: 100%;
      max-width: 459px;
      margin-right: 47px;
    }

    .introduction-wrapper {
      padding-left: 7px;
    }

    .banner-image {
      position: relative;
      padding-top: calc(286 / 459 * 100%);

      img {
        position: absolute;
        top: 0;
        left: 0;
      }
    }

    .introduction-content {
      font-size: $fontsize-title;
      line-height: calc(32 / 17);
    }

    .title-common-primary {
      margin-bottom: 92px;
    }

    .title-common-product {
      margin-bottom: 32px;
    }

    .title-common-product.is-large {
      margin-bottom: 15px;
    }
  }

  @include mobile {
    margin: 0.4rem 0 0.8rem;

    .introduction-banner {
      margin-bottom: 0.3rem;
    }
  }
}

.section-product-merit {
  .content-detail {
    color: $color-dark;
    letter-spacing: $letter-spacing-reset;

    .detail-text {
      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  @include desktop {
    margin-bottom: 223px;

    .wrapper {
      max-width: 928px;
    }

    .title-common-primary {
      margin-bottom: 145px;
    }

    .title-common-product.is-large {
      letter-spacing: 0.13em;
    }

    .content-detail {
      font-size: $fontsize-title;
      line-height: calc(32 / 17);
    }
  }

  @include mobile {
    margin-bottom: 0.8rem;
  }
}

.section-product-case {
  .examples-detail {
    color: $color-dark;
    letter-spacing: $letter-spacing-reset;

    .detail-text {
      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  @include desktop {
    margin-bottom: 214px;

    .wrapper {
      max-width: 920px;
    }

    .examples-detail {
      margin-bottom: 76px;
      font-size: $fontsize-md;
      line-height: calc(33 / 18);
    }

    .title-common-primary {
      margin-bottom: 96px;
    }
  }

  @include mobile {
    margin-bottom: 1rem;

    .examples-detail {
      margin-bottom: 0.3rem;
    }
  }
}

.list-product-merit {
  .list-wrapper {
    @include reset-list;
  }

  .list-item {
    &:last-child {
      margin-bottom: 0;
    }
  }

  .image-block {
    &:last-child {
      margin-bottom: 0;
    }
  }

  .list-item.is-efficiency {
    .content-image {
      width: 1.01rem;
    }
  }

  .list-item.is-satisfaction {
    .image-block {
      margin-bottom: 0.06rem;
    }
  }

  @include desktop {
    padding-left: 7px;

    .item-content {
      display: flex;
    }

    .content-image {
      flex: 0 0 auto;
    }

    .list-item {
      margin-bottom: 218px;
    }

    .title-common-product.is-large {
      margin-bottom: 36px;
    }

    .list-item.is-efficiency {
      .image-block {
        margin-top: -22px;
      }

      .item-content {
        flex-direction: row-reverse;
      }

      .content-detail {
        margin-right: 32px;
      }
    }

    .list-item.is-satisfaction {
      .image-block {
        width: 459px;
        height: 241px;
      }

      .content-image {
        margin-right: 48px;
      }

      .content-detail {
        margin-top: -11px;
      }
    }
  }

  @include mobile {
    .list-item {
      margin-bottom: 0.6rem;
    }

    .content-image {
      margin: 0 auto 0.4rem;
    }
  }
}
