@charset "utf-8";

.paragraph-common {
  letter-spacing: 0.13em;

  p {
    margin-bottom: 0;
  }

  &.is-spacing {
    font-feature-settings: "palt";
  }

  @include desktop {
    max-width: 918px;
    margin: 0 auto 166px;
    font-size: $fontsize-md;
    line-height: calc(32.96 / 17.98);
  }

  @include mobile {
    margin: 0 auto 0.5rem;
    font-size: $fontsize-default-sp;
    line-height: $line-height-sp;
  }
}
