@charset "utf-8";

.footer-common {
  .footer-wrapper {
    font-weight: $fontweight-bold;
  }

  @include desktop {
    .footer-wrapper {
      padding: 9px 0 45px;
    }
  }

  @include mobile {
    .footer-wrapper {
      padding: 0.16rem 0;
    }
  }
}

.contact-common {
  background-color: $color-background-title;
  color: $color-light;
  font-weight: $fontweight-bold;

  .contact-title {
    text-align: center;
  }

  .block-time {
    margin-bottom: 10px;

    time {
      margin-left: -0.05rem;
    }
  }

  .block-telephone {
    position: relative;
    line-height: 1;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      background: url("../img/common/logo_primary_03.png") 50% 50% / 100% auto no-repeat;
    }
  }

  @include desktop {
    padding: 60px 0 73px;

    .contact-title {
      margin-bottom: 42px;
      font-size: 0.31rem;
    }

    .contact-wrapper {
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .block-time {
      margin-bottom: 0;
      font-size: $fontsize-paragraph;
    }

    .block-telephone {
      padding-left: 60px;
      font-size: 0.41rem;
      pointer-events: none;

      &::before {
        width: 57px;
      }
    }
  }

  @include mobile {
    padding: 0.5rem 0;

    .contact-title {
      margin-bottom: 0.3rem;
      font-size: $fontsize-lg;
    }

    .block-time {
      font-size: $fontsize-title;
      line-height: $line-height-title;
    }

    .block-telephone {
      padding-left: 0.5rem;
      font-size: 0.3rem;

      &::before {
        width: 0.47rem;
      }
    }
  }
}

.block-common-phone.is-cta {
  display: flex;
  flex-direction: column;
  justify-content: center;

  .tel-number {
    position: relative;
  }

  @include mobile {
    align-items: center;
  }
}

.button-common-contact {
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;

  .button-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    padding: 0.09rem 0.2rem 0.06rem;
    background-color: $color-background-button;
    color: $color-dark;
  }

  @include desktop {
    width: 392px;
    min-height: 55px;
    margin-right: 46px;
    border-radius: 0.26rem;

    .button-wrapper {
      font-size: 0.23rem;
    }
  }

  @include mobile {
    width: 100%;
    max-width: 3rem;
    margin: 0 auto 0.2rem;
    border-radius: 0.4rem;

    .button-wrapper {
      min-height: 0.52rem;
      padding: 0.11rem 0.3rem 0.08rem;
      font-size: $fontsize-lg;
    }
  }
}

.footer-detail {
  .detail-text {
    letter-spacing: 0.08em;

    &:last-child {
      margin-bottom: 0;
    }
  }

  @include desktop {
    margin-bottom: 53px;
  }

  @include mobile {
    margin-bottom: 0.3rem;
    font-size: 0.15rem;
  }
}

.list-common-sitemap {
  display: flex;
  margin: -0.1rem;
  padding: 0;
  list-style: none;

  .list-item {
    position: relative;
    padding: 0.1rem;

    &::before {
      content: "";
      position: absolute;
      top: 0.15rem;
      right: 0;
      bottom: 0.15rem;
      border-right: 1px solid $color-dark;
    }

    &:last-child {
      &::before {
        content: none;
      }
    }
  }

  @include desktop {
    .list-item {
      font-size: 0.15rem;
    }
  }

  @include mobile {
    .list-item {
      font-size: $fontsize-title;
    }
  }
}

.footer-information {
  @include desktop {
    .information-address {
      margin-bottom: 4px;
      font-size: $fontsize-paragraph;
    }
  }

  @include mobile {
    .information-address {
      margin-bottom: 0.1rem;
      font-size: $fontsize-title;
    }
  }
}

.footer-copyright {
  background-color: $color-dark;
  font-weight: 300;
  text-align: center;

  .copyright-content {
    color: $color-light;
  }

  @include desktop {
    padding: 0.09rem 0 0.07rem;
  }

  @include mobile {
    padding: 0.08rem 0 0.97rem;
    font-size: $fontsize-title;
    line-height: $line-height-title;
  }
}

.navigation-footer-sitemap {
  @include mobile {
    display: flex;
    justify-content: center;
  }
}

.button-common-top {
  display: none;
  position: fixed;
  z-index: 100;
  cursor: pointer;

  @include desktop {
    right: 30px;
    bottom: 56px;
    width: 56px;
  }

  @include mobile {
    right: 0.15rem;
    bottom: 1.08rem;
    width: 0.5rem;
  }
}

.button-footer-contact {
  @include mobile {
    display: flex;
    position: fixed;
    z-index: 99;
    bottom: 0.1rem;
    left: 50%;
    align-items: center;
    justify-content: center;
    width: 94%;
    min-height: 0.8rem;
    transform: translateX(-50%);
    border: solid $color-light;
    border-radius: 0.11rem;
    background-color: $color-background-title;
    box-shadow: 0.02rem 0.04rem 0.04rem rgba(0, 0, 0, 0.25);
    color: $color-background-button;
    font-size: 0.34rem;
    font-weight: $fontweight-bold;
    letter-spacing: 0.1em;
    text-align: center;
  }
}
