@charset "utf-8";

$viewport-width: 1280px;
$wrapper-width: 1020px;
$font-sans: "游ゴシック", yugothic, "Yu Gothic", "ヒラギノ角ゴ ProN W3", "Hiragino Kaku Gothic ProN", "ヒラギノ角ゴ", "Hiragino Sans", "メイリオ", meiryo,
  sans-serif;
$fontsize-default-pc: 0.14rem;
$fontsize-default-sp: 0.23rem;
$fontsize-lg: 0.2rem;
$fontsize-md: 0.18rem;
$fontsize-paragraph: 0.16rem;
$fontsize-title: 0.17rem;
$fontweight-default: 500;
$fontweight-semibold: 600;
$fontweight-bold: 700;
$line-height-pc: calc(24 / 14);
$line-height-sp: calc(45 / 23);
$line-height-title: calc(28 / 17);
$letter-spacing-default: 0.12em;
$letter-spacing-paragraph: -0.02em;
$letter-spacing-title: 0.03em;
$letter-spacing-reset: 0;
$color-default: #211915;
$color-dark: #000;
$color-light: #fff;
$color-gray: #e6edf4;
$color-background-title: #004098;
$color-background-button: #fff200;
