@charset "utf-8";

.header-common {
  z-index: 9999;
  background-color: $color-light;

  .navigation-inner {
    display: flex;
  }

  @include desktop {
    position: relative;
    min-width: $viewport-width;
    padding: 21px 0 74px;

    .navigation-inner {
      position: absolute;
      top: calc(50% - 62px);
      right: calc(50% - 510px);
      align-items: center;
      padding: 8px 0;
    }
  }

  @include mobile {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 0.94rem;

    .navigation-inner {
      justify-content: center;
      padding: 0.2rem 0.5rem 0.5rem;
    }
  }
}

.header-common.is-scrolled {
  @include desktop {
    .menu-header-sitemap {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      min-width: $viewport-width;
    }
  }

  @include print {
    .menu-header-sitemap {
      position: absolute;
    }
  }
}

.menu-header-sitemap {
  border-top: 1px solid #bbb;

  @include reset-text;

  .menu-wrapper {
    display: flex;
    flex-wrap: wrap;

    @include reset-list;
  }

  @include desktop {
    position: absolute;
    top: calc(100% - 58px);
    left: 0;
    width: 100%;
    padding: 12px 0;
    background-color: $color-light;

    .menu-viewport {
      max-width: 1020px;
      margin: 0 auto;
    }

    .menu-wrapper {
      align-items: center;
      margin: -10px -23px;

      html.is-mac & {
        margin: -10px -20px;
      }
    }

    .menu-child {
      padding: 10px 23px;

      html.is-mac & {
        padding: 10px 20px;
      }
    }
  }

  @include mobile {
    padding: 0.3rem 0.5rem 0;

    .menu-wrapper {
      flex-direction: column;
    }

    .menu-child {
      padding: 0.1rem 0;
    }
  }
}

.list-header-sitemap {
  cursor: default;

  .list-wrapper {
    color: $color-default;

    @include reset-list;
  }

  .list-item {
    &:last-child {
      margin-bottom: 0;
    }
  }

  @include desktop {
    position: absolute;
    top: 42px;
    left: -19px;
    width: 142px;
    min-height: 50px;
    padding: 14px 10px 12px;
    transition: opacity 0.3s ease;
    opacity: 0;
    background-color: $color-gray;
    pointer-events: none;

    .list-item {
      margin-bottom: 10px;
    }

    html.is-mac & {
      top: 45px;
      width: 145px;
    }
  }

  @include mobile {
    padding: 0.2rem 0.14rem;
  }
}

.label-header-primary {
  position: relative;

  .label-wrapper {
    position: relative;
    transition:
      opacity 0.3s ease,
      visibility 0.3s ease,
      color 0.3s ease,
      background 0.3s ease;
    color: $color-default;
    letter-spacing: 0.1em;
    text-decoration: none;
    cursor: pointer;

    &::before {
      content: "";
      visibility: hidden;
      position: absolute;
      right: 0;
      left: 0;
      height: 0.01rem;
      transition:
        opacity 0.3s ease,
        visibility 0.3s ease,
        color 0.3s ease,
        background 0.3s ease;
      border-bottom: 1px solid $color-background-title;
      opacity: 0;
    }
  }

  .label-wrapper {
    &:hover {
      color: $color-background-title;

      &::before {
        visibility: visible;
        opacity: 1;
      }
    }
  }

  &:hover {
    .list-header-sitemap {
      opacity: 1;
      pointer-events: auto;
    }
  }

  &.is-current {
    .label-wrapper {
      color: $color-background-title;

      &::before {
        visibility: visible;
        opacity: 1;
      }
    }
  }

  .label-text {
    position: relative;
  }

  html.is-touchable & {
    cursor: default;
  }

  @include desktop {
    .label-wrapper {
      position: relative;
      top: 0.03rem;
      padding-bottom: 6px;
      font-size: $fontsize-title;
    }

    .label-wrapper {
      &::before {
        bottom: 0;
      }
    }
  }

  @include mobile {
    .label-wrapper {
      font-size: $fontsize-lg;

      &::before {
        bottom: -0.05rem;
      }
    }
  }
}

.label-header-primary.has-submenu {
  @include desktop {
    &::before {
      content: "";
      position: absolute;
      bottom: -20px;
      left: 0;
      width: 100%;
      height: 20px;
      opacity: 0;
      cursor: default;
      pointer-events: none;
    }

    &:hover {
      &::before {
        opacity: 1;
        pointer-events: all;
      }

      .label-wrapper {
        color: $color-background-title;

        &::before {
          visibility: visible;
          opacity: 1;
        }
      }
    }
  }
}

.label-header-primary.is-top {
  .label-wrapper {
    display: block;

    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      transition: filter 0.3s ease;
      background: url("../img/common/icon_home_01.png") 50% 50% / 100% auto no-repeat;
      filter: brightness(0) invert(0);
    }
  }

  &:hover {
    .label-wrapper {
      &::after {
        filter: unset;
      }
    }
  }

  @include desktop {
    .label-wrapper {
      top: 0;
      width: 0.29rem;
      height: 0.29rem;

      &::before {
        bottom: -6px;
      }
    }
  }

  @include mobile {
    .label-wrapper {
      width: 0.36rem;
      height: 0.36rem;
    }
  }
}

.label-header-primary.is-top.is-current {
  .label-wrapper {
    &::after {
      filter: unset;
    }
  }
}

.block-header-contact {
  font-weight: $fontweight-bold;
  letter-spacing: $letter-spacing-paragraph;

  .block-title {
    line-height: calc(18 / 16);
  }

  .block-telephone {
    position: relative;
    line-height: 1;
  }

  @include desktop {
    margin-right: 18px;

    .block-title {
      margin-bottom: 6px;
      font-size: $fontsize-paragraph;
    }

    .block-telephone {
      padding-left: 0.5rem;
      font-size: 0.3rem;
      pointer-events: none;

      &::before {
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        width: 0.43rem;
        background: url("../img/common/logo_primary_04.png") 50% 50% / 100% auto no-repeat;
      }
    }
  }

  @include mobile {
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 50%;
    margin-right: 0.1rem;
    padding: 0.1rem 0;
    border: 0.05rem solid $color-dark;
    border-radius: 0.1rem;
    text-align: center;

    .block-title {
      margin-bottom: 0.05rem;
      font-size: $fontsize-paragraph;
    }

    .block-telephone {
      font-size: 0.26rem;

      html.is-iphone & {
        font-size: 0.2rem;
      }
    }
  }
}

.button-header-contact {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 0.51rem;
  overflow: hidden;
  background-color: $color-background-title;
  color: $color-background-button;
  font-weight: $fontweight-bold;

  .button-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.1rem 0.16rem 0.06rem;
  }

  @include desktop {
    width: 157px;
    border-radius: 0.05rem;
    font-size: 0.19rem;
  }

  @include mobile {
    flex: 1;
    width: 50%;
    border-radius: 0.1rem;
    font-size: 0.22rem;
  }
}

.navigation-header-sitemap {
  @include desktop {
    .navigation-wrapper {
      display: block !important;
    }
  }

  @include mobile {
    .navigation-wrapper {
      display: none;
      position: fixed;
      z-index: 10;
      top: 0.93rem;
      right: 0;
      bottom: 0;
      left: 0;
      overflow: hidden auto;
      background-color: $color-light;
      pointer-events: none;

      @include scroll-hidden;
    }

    .navigation-wrapper.is-active {
      pointer-events: auto;
    }
  }
}

.button-header-burger {
  @include mobile {
    position: fixed;
    z-index: 10;
    top: 0;
    right: 0;
    width: 0.77rem;
    height: 0.94rem;
    cursor: pointer;

    .button-line {
      position: absolute;
      top: calc(50% - 0.01rem);
      right: 0;
      left: 0;
      width: 0.32rem;
      height: 0.03rem;
      margin: 0 auto;
      transition:
        opacity 0.3s ease,
        transform 0.3s ease;
      background-color: $color-dark;
    }

    .button-line:nth-child(1) {
      transform: translateY(-0.08rem);
    }

    .button-line:nth-child(3) {
      transform: translateY(0.08rem);
    }
  }
}

.button-header-burger.is-active {
  @include mobile {
    .button-line:nth-child(1) {
      transform: rotate(45deg) scale(calc(25 / 20), 1);
    }

    .button-line:nth-child(2) {
      opacity: 0;
    }

    .button-line:nth-child(3) {
      transform: rotate(-45deg) scale(calc(25 / 20), 1);
    }
  }
}

.label-header-secondary {
  position: relative;
  transition: color 0.3s ease;
  text-decoration: none;

  &:hover {
    color: $color-background-title;
  }

  &.is-current {
    color: $color-background-title;
  }

  @include desktop {
    font-size: $fontsize-default-pc;
  }

  @include mobile {
    font-size: $fontsize-title;
  }
}

.button-skip-link {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
  border: 0;
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
}

.button-skip-link:focus {
  display: block;
  z-index: 100;
  top: 5px;
  left: 5px;
  width: auto;
  height: auto;
  padding: 15px 23px 14px;
  clip: auto;
  background-color: #eee;
  color: #444;
  font-size: 1em;
  line-height: normal;
  text-decoration: none;
  -webkit-clip-path: none;
  clip-path: none;
}
