@charset "utf-8";

.section-top-keyvisual {
  position: relative;

  .heading-banner {
    position: relative;
    z-index: 1;

    img {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }
  }

  .keyvisual-wrapper {
    display: flex;
    position: absolute;
    z-index: 2;
    flex-direction: column;
    align-items: flex-start;
  }

  .keyvisual-title {
    position: relative;
    letter-spacing: 0.04em;
    text-align: center;
    font-feature-settings: "palt";
  }

  .keyvisual-subtitle {
    color: $color-background-title;
    font-weight: $fontweight-bold;
    letter-spacing: 0.04em;
    font-feature-settings: "palt";
  }

  @include desktop {
    .heading-banner {
      min-height: 582px;
      padding-top: calc(582 / 1920 * 100%);
    }

    .keyvisual-wrapper {
      top: calc(50% - 203px);
      left: calc(50% - 568px);
    }

    .keyvisual-title {
      margin-bottom: 27px;
      padding: 0 57px;
      font-size: 0.38rem;
      line-height: calc(55 / 38);

      &::before,
      &::after {
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        width: 0.41rem;
        background: url("../img/top/decoration_top_01.svg") 50% 50% / 100% auto no-repeat;
      }

      &::before {
        left: 0;
      }

      &::after {
        right: 0;
        transform: scaleX(-1);
        filter: fliph;
      }
    }

    .keyvisual-subtitle {
      margin-left: 57px;
      font-size: 0.25rem;

      .subtitle-wrapper {
        text-underline-offset: 7px;
        text-decoration: underline;
        text-decoration-color: $color-background-button;
        text-decoration-thickness: 0.03rem;
      }
    }
  }

  @include mobile {
    .heading-banner {
      min-height: 3.61rem;
      padding-top: calc(386 / 500 * 100%);
    }

    .keyvisual-wrapper {
      top: 0.27rem;
      left: 0.1rem;

      html.is-ios & {
        margin-left: -0.1rem;
      }
    }

    .keyvisual-title {
      margin-bottom: 0.06rem;
      font-size: 0.24rem;
      line-height: calc(40 / 24);

      html.is-ios & {
        min-width: 2.5rem;
      }

      .title-wrapper {
        position: relative;
        padding: 0 0.1rem;
        background: linear-gradient(to right, $color-light);
        box-decoration-break: clone;

        html.is-android & {
          top: -0.01rem;
          padding: 0 0.1rem 0.01rem;
        }
      }
    }

    .keyvisual-subtitle {
      margin-left: 0.1rem;
      padding: 0 0.04rem;
      background-color: #fff20e;
      font-size: 0.16rem;

      .subtitle-wrapper {
        text-underline-offset: 0.05rem;
        position: relative;
        top: 0.01rem;

        html.is-ios & {
          top: 0;
        }

        html.is-android & {
          top: -0.01rem;
        }
      }
    }
  }
}

.section-top-inquiry {
  .inquiry-subtile {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    border-radius: 0.16rem;
    background-color: $color-background-title;
    color: $color-light;
    font-weight: $fontweight-bold;
    letter-spacing: 0.1em;
    font-feature-settings: "palt";

    .subtile-wrapper {
      position: relative;
      top: 0.01rem;
    }
  }

  @include desktop {
    padding: 87px 0 143px;

    .inquiry-subtile {
      width: 626px;
      min-height: 98px;
      padding: 10px 20px;
      font-size: 0.37rem;
    }

    .title-common-secondary {
      margin-bottom: 20px;
    }
  }

  @include mobile {
    padding: 0.6rem 0;

    .inquiry-subtile {
      width: 4rem;
      min-height: 0.8rem;
      padding: 0.1rem;
      font-size: 0.23rem;
    }
  }
}

.section-top-problem {
  background-color: $color-gray;

  .problem-wrapper {
    position: relative;
    border-radius: 0.23rem;
    background-color: $color-light;

    &::before,
    &::after {
      content: "";
      position: absolute;
    }

    &::before {
      background: url("../img/top/img_problem_01.png") 50% 50% / 100% auto no-repeat;
    }

    &::after {
      background: url("../img/top/img_problem_02.png") 50% 50% / 100% auto no-repeat;
    }
  }

  @include desktop {
    padding: 155px 0 198px;

    .wrapper {
      width: calc(100% - 180px);
      max-width: 1168px;
    }

    .problem-wrapper {
      padding: 76px 99px 69px;

      &::before {
        bottom: -35px;
        left: -82px;
        width: 183px;
        height: 273px;
      }

      &::after {
        right: -98px;
        bottom: -47px;
        width: 211px;
        height: 314px;
      }
    }

    .list-top-checks {
      max-width: 962px;
      margin: 0 auto;
    }

    .title-common-secondary {
      margin-bottom: 37px;
    }
  }

  @include laptop-md {
    .problem-wrapper {
      padding: 76px 99px 69px;

      &::before {
        bottom: -35px;
        left: -62px;
        width: 143px;
        height: 233px;
      }

      &::after {
        right: -78px;
        bottom: -47px;
        width: 171px;
        height: 274px;
      }
    }
  }

  @include mobile {
    padding: 0.7rem 0 1.4rem;

    .problem-wrapper {
      padding: 0.4rem 0.2rem;

      &::before {
        bottom: -1rem;
        left: -0.2rem;
        width: 0.915rem;
        height: 1.365rem;
      }

      &::after {
        right: -0.2rem;
        bottom: -1.1rem;
        width: 1.055rem;
        height: 1.57rem;
      }
    }
  }
}

.list-top-checks {
  .list-wrapper {
    @include reset-list;

    display: flex;
    flex-wrap: wrap;
  }

  .list-item {
    color: $color-dark;
    font-weight: $fontweight-bold;
    letter-spacing: $letter-spacing-reset;
  }

  .item-title {
    display: block;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      background: url("../img/common/icon_check_01.svg") 50% 50% / 100% auto no-repeat;
    }
  }

  @include desktop {
    .list-wrapper {
      margin: -7px 0;
    }

    .item-title {
      padding-right: 20px;
      padding-left: 43px;
      font-size: 0.29rem;

      &::before {
        top: 8px;
        width: 33px;
        height: 33px;
      }
    }

    .list-item {
      width: 50%;
      padding: 7px 0;

      &.is-spacing {
        .item-title {
          margin-right: -47px;
        }
      }
    }
  }

  @include laptop-md {
    .item-title {
      padding-left: 38px;
      font-size: 0.25rem;

      &::before {
        top: 7px;
        width: 30px;
        height: 30px;
      }
    }
  }

  @include mobile {
    .list-item {
      flex-direction: column;
    }

    .list-wrapper {
      margin: -0.05rem 0;
    }

    .list-item {
      width: 100%;
      padding: 0.05rem 0;
    }

    .item-title {
      padding-left: 0.33rem;
      font-size: 0.22rem;

      &::before {
        top: 0.06rem;
        width: 0.26rem;
        height: 0.26rem;
      }

      html.is-android & {
        &::before {
          top: 0.07rem;
        }
      }
    }
  }
}

.section-top-information {
  @include desktop {
    padding: 184px 0 179px;

    .information-product {
      margin-bottom: 185px;
    }

    .title-common-primary {
      margin-bottom: 81px;
    }
  }

  @include mobile {
    padding: 1rem 0;

    .information-product {
      margin-bottom: 1rem;
    }
  }
}

.navigation-top {
  .navigation-wrapper {
    @include reset-list;
  }

  @include desktop {
    .navigation-wrapper {
      display: flex;
      flex-wrap: wrap;
      margin: -21.5px -20px;
    }

    .navigation-item {
      width: calc(100% / 3);
      padding: 21.5px 20px;
    }

    .item-image {
      width: 100%;
      max-width: 310px;
      margin-bottom: 25px;
    }

    .image-wrapper {
      position: relative;
      padding-top: calc(223 / 310 * 100%);

      img {
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }

  @include mobile {
    .navigation-item {
      margin-bottom: 0.6rem;

      &:last-child {
        margin-bottom: 0;
      }
    }

    .item-image {
      width: 3.1rem;
      margin: 0 auto 0.2rem;
    }
  }
}

.list-top-information {
  .list-wrapper {
    @include reset-list;
  }

  @include desktop {
    .list-item {
      margin-bottom: 184px;
    }

    .list-item {
      .product-image {
        max-width: 600px;
      }

      .image-block {
        padding-top: calc(430 / 600 * 100%);
      }
    }

    .list-item.is-second {
      .image-block {
        padding-top: calc(463 / 600 * 100%);
      }
    }

    .block-common-product {
      justify-content: space-between;

      .product-content {
        max-width: 364px;
        padding-top: 14px;
      }

      .content-detail {
        margin-bottom: 55px;
      }
    }
  }

  @include mobile {
    .list-item {
      margin-bottom: 1.3rem;
    }
  }
}
