@charset "utf-8";

.section-about-introduction {
  .content-image {
    width: 1.08rem;
    height: 1.38rem;
    margin-right: 0.37rem;

    &:last-child {
      margin-right: 0;
    }
  }

  .content-wrapper {
    display: flex;
  }

  .content-detail {
    font-size: $fontsize-md;
    letter-spacing: 0.08em;
    line-height: calc(33 / 18);
    font-feature-settings: "palt";

    .detail-text {
      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  @include desktop {
    padding: 115px 0 192px;

    .introduction-content {
      display: flex;
      max-width: 766px;
      margin: 0 auto 191px;

      html.is-mac & {
        max-width: 770px;
      }
    }

    .introduction-wrapper {
      padding-top: 20px;
    }

    .content-detail {
      padding: 14px 21px 14px 3px;
    }

    .title-common-primary {
      margin-bottom: 107px;
    }

    .title-common-secondary {
      margin-bottom: 89px;
    }
  }

  @include mobile {
    padding: 0.4rem 0 0.8rem;

    .introduction-content {
      margin-bottom: 1rem;
    }

    .content-detail {
      margin-bottom: 0.4rem;
    }

    .content-wrapper {
      align-items: center;
      justify-content: center;
    }
  }
}

.section-about-analyze {
  background-color: $color-gray;

  @include desktop {
    padding: 186px 0 145px;

    .title-common-primary {
      margin-bottom: 65px;
    }
  }

  @include mobile {
    padding: 0.8rem 0 1rem;
  }
}

.title-about-analyze {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 0;
  background-color: #fff20e;
  color: $color-background-title;
  font-size: 0.31rem;
  font-weight: $fontweight-bold;
  letter-spacing: 0.15em;
  text-align: center;

  .title-wrapper {
    display: inline-block;
    position: relative;

    &::before,
    &::after {
      content: "";
      position: absolute;
      top: calc(50% - 0.25rem);
      width: 0.63rem;
      height: 0.51rem;
    }

    &::before {
      left: 0;
    }

    &::after {
      right: 0;
      -moz-transform: scaleX(-1);
      -o-transform: scaleX(-1);
      -webkit-transform: scaleX(-1);
      transform: scaleX(-1);
      -ms-filter: "FlipH";
      filter: fliph;
    }
  }

  @include desktop {
    padding: 0.16rem 0.2rem;

    .title-wrapper {
      padding: 0 80px;
    }
  }

  @include mobile {
    padding: 0.16rem 0.1rem;

    .title-wrapper {
      padding: 0 0.7rem;
    }
  }
}

.block-about-point {
  border: 0.03rem solid $color-background-title;
  border-radius: 0.41rem;

  .content-banner {
    width: 3.14rem;
    margin: 0 auto;
  }

  .block-content {
    position: relative;
  }

  .content-title {
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
    text-align: center;

    .title-wrapper {
      display: block;
      position: relative;
    }

    .title-layer.is-upper {
      position: relative;
      z-index: 1;
      color: #fff20e;
    }

    .title-layer.is-below {
      position: absolute;
      top: 0;
      right: -0.2rem;
      bottom: 0;
      left: -0.2rem;
      color: $color-background-title;
      -webkit-text-stroke: 0.09rem $color-background-title;
    }
  }

  .content-describe {
    margin-bottom: 0.3rem;
    font-size: 0.29rem;
    font-weight: $fontweight-bold;
    letter-spacing: $letter-spacing-paragraph;
    line-height: calc(47 / 29);
    text-align: center;

    .describe-number {
      font-size: 0.37rem;
      line-height: calc(47 / 37);
    }

    .describe-text {
      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  .detail-label {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    border-radius: 0.16rem;
    background-color: $color-background-title;
    color: $color-light;
    font-weight: $fontweight-bold;
    letter-spacing: $letter-spacing-paragraph;
    line-height: 1;
    text-align: center;
  }

  .detail-wrapper {
    display: flex;
  }

  @include desktop {
    position: relative;
    max-width: 1017px;
    margin: 0 auto;
    padding: 101px 33px 45px;

    &::before {
      content: "";
      position: absolute;
      z-index: -1;
      top: calc(50% - 300px);
      bottom: calc(50% - 347px);
      left: 50%;
      transform: translateX(-50%);
      border-right: 0.02rem solid #dae3ea;
    }

    .block-wrapper {
      display: flex;
      margin-bottom: 48px;
    }

    .block-content {
      width: calc(100% / 2);
    }

    .content-title {
      position: absolute;
      top: -1.59rem;
      right: 0;
      left: 0;
      font-size: 0.66rem;

      .title-wrapper {
        padding-left: 5px;
        background-color: $color-light;
      }

      .title-layer.is-below {
        left: -15px;
      }
    }

    .block-detail {
      max-width: 8.03rem;
      margin: 0 auto;
    }

    .detail-label {
      min-height: 33px;
      margin-bottom: 28px;
      padding: 5px 20px 2px;
      font-size: 0.25rem;
    }

    .detail-wrapper {
      gap: 30px;
    }
  }

  @include mobile {
    padding: 0 0.3rem 0.6rem;

    .content-title {
      margin-bottom: 0.3rem;
      font-size: 0.6rem;
    }

    .block-content {
      padding: 0.5rem 0 0.8rem;

      &:last-child {
        border: none;
      }
    }

    .detail-label {
      min-height: 0.6rem;
      margin-bottom: 0.2rem;
      padding: 0.1rem;
      font-size: $fontsize-lg;
      line-height: calc(30 / 20);
    }

    .detail-wrapper {
      gap: 0.1rem;
    }
  }
}

.list-about-analyze {
  .list-wrapper {
    @include reset-list;
  }

  .about-image {
    width: 0.8rem;

    &.is-large {
      width: 0.83rem;
    }
  }

  .diagram-about {
    display: flex;
    align-items: center;
  }

  .about-title {
    position: relative;
    color: $color-background-title;
    font-weight: $fontweight-bold;
    letter-spacing: 0.15em;
    font-feature-settings: "palt";

    &::before {
      content: "";
      position: absolute;
      right: 0;
      left: 0;
      height: 0.03rem;
      border-bottom: 0.03rem solid #fff20e;
      text-decoration: underline;
    }

    .title-number {
      position: relative;
      top: 0.04rem;
      letter-spacing: $letter-spacing-title;
    }
  }

  .item-wrapper {
    overflow: hidden;
    border-radius: 0.41rem;
    background-color: $color-light;
  }

  .item-detail {
    color: $color-dark;
    font-weight: $fontweight-default;
    letter-spacing: 0.15em;
    text-align: right;
  }

  .list-item {
    &:last-child {
      margin-bottom: 0;
    }
  }

  .list-item.is-first {
    .title-wrapper {
      &::before,
      &::after {
        background: url("../img/about/icon_analyze_01.png") 50% 50% / 100% auto no-repeat;
      }
    }
  }

  .list-item.is-second {
    .title-wrapper {
      &::before,
      &::after {
        background: url("../img/about/icon_analyze_02.png") 50% 50% / 100% auto no-repeat;
      }
    }
  }

  .list-item.is-third {
    .title-wrapper {
      &::before,
      &::after {
        background: url("../img/about/icon_analyze_03.png") 50% 50% / 100% auto no-repeat;
      }
    }
  }

  @include desktop {
    .item-content {
      display: flex;
      align-items: center;
      margin: 0 auto;
    }

    .about-title {
      flex: 1;
      font-size: 0.24rem;

      &::before {
        bottom: 0.125rem;
      }

      html.is-mac & {
        &::before {
          bottom: 0.08rem;
        }
      }

      .title-number {
        font-size: 0.51rem;
      }
    }

    .diagram-about {
      flex: 1;

      &:last-child {
        margin-right: 0 !important;
      }
    }

    .about-image {
      flex: 0 0 auto;
    }

    .list-item {
      margin-bottom: 106px;
    }

    .list-item.is-first {
      .item-content {
        max-width: 700px;
        padding: 76px 0 54px;

        html.is-mac & {
          max-width: 740px;
        }
      }

      .diagram-about {
        margin-right: 120px;
      }

      .about-image {
        margin-right: 18px;
      }
    }

    .list-item.is-second {
      .diagram-about {
        flex-direction: column;
      }

      .item-content {
        max-width: 794px;
        padding: 69px 0 31px;
      }

      .about-image {
        margin-bottom: 4px;
      }
    }

    .list-item.is-third {
      .item-content {
        padding: 63px 87px 23px;
      }
    }

    .item-detail {
      padding: 10px 35px;
      font-size: 0.11rem;
    }
  }

  @include mobile {
    .item-detail {
      padding: 0.1rem 0.2rem;
      font-size: 0.14rem;
    }

    .about-title {
      flex: 1;
      font-size: 0.22rem;

      &::before {
        bottom: 0.075rem;
      }

      .title-number {
        font-size: 0.41rem;
      }
    }

    .diagram-about {
      flex-direction: column;
      margin-bottom: 0.4rem;

      &:last-child {
        margin-bottom: 0;
      }
    }

    .item-content {
      padding: 0.4rem 0.2rem 0.2rem;
    }

    .list-item {
      margin-bottom: 0.6rem;
    }
  }
}

.diagram-about-pie {
  .diagram-wrapper {
    @include reset-list;

    display: flex;
    flex-wrap: wrap;
  }

  .item-chart {
    position: relative;
    width: 1.4rem;
    height: 1.4rem;
  }

  .chart-content {
    display: flex;
    position: absolute;
    top: 50%;
    left: 50%;
    align-items: center;
    justify-content: center;
    width: 0.81rem;
    height: 0.81rem;
    overflow: hidden;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    background-color: $color-light;
    color: $color-dark;
    font-size: 0.35rem;

    @include reset-text;

    .content-unit {
      font-size: 0.19rem;
    }
  }

  .item-percent {
    text-align: center;

    .percent-text {
      position: relative;
      color: $color-background-title;
      font-weight: $fontweight-bold;
      letter-spacing: 0.07em;
      font-feature-settings: "palt";

      &::before {
        content: "";
        position: absolute;
        right: 0;
        left: 0;
        height: 0.03rem;
        border-bottom: 0.03rem solid #fff20e;
        text-decoration: underline;
      }
    }
  }

  .diagram-item.is-first {
    .item-chart {
      background: url("../img/about/img_analyze_01.png") 50% 50% / 100% auto no-repeat;
    }
  }

  .diagram-item.is-second {
    .item-chart {
      background: url("../img/about/img_analyze_02.png") 50% 50% / 100% auto no-repeat;
    }
  }

  .diagram-item.is-third {
    .item-chart {
      background: url("../img/about/img_analyze_03.png") 50% 50% / 100% auto no-repeat;
    }
  }

  .diagram-item.is-four {
    .item-chart {
      background: url("../img/about/img_analyze_04.png") 50% 50% / 100% auto no-repeat;
    }
  }

  .diagram-item.is-five {
    .item-chart {
      background: url("../img/about/img_analyze_05.png") 50% 50% / 100% auto no-repeat;
    }
  }

  @include desktop {
    .diagram-wrapper {
      margin: 0 -19px;
    }

    .diagram-item {
      width: calc(100% / 5);
      padding: 0 19px;
    }

    .percent-text {
      font-size: 0.4rem;

      &::before {
        bottom: 3px;
      }

      html.is-mac & {
        &::before {
          bottom: -4px;
        }
      }
    }

    .percent-unit {
      font-size: 0.25rem;
    }
  }

  @include mobile {
    max-width: 3.8rem;
    margin: 0 auto;

    .diagram-wrapper {
      margin: -0.2rem -0.1rem;
    }

    .diagram-item {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: calc(100% / 2);
      padding: 0.2rem 0.1rem;
    }

    .item-chart {
      margin-bottom: 0.05rem;
    }

    .percent-text {
      font-size: 0.3rem;

      &::before {
        bottom: 0.01rem;
      }
    }

    .percent-unit {
      font-size: $fontsize-lg;
    }
  }
}
