@charset "utf-8";

.navigation-common-sitemap {
  .navigation-wrapper {
    display: flex;
    flex-wrap: wrap;
    padding: 0;
    list-style: none;
  }

  .item-link {
    display: inline-block;
    position: relative;
    padding-left: 0.2rem;
    color: $color-dark;
    font-weight: $fontweight-default;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      width: 0.16rem;
      margin-top: -0.04rem;
      background: url("../img/common/icon_arrow_04.png") 50% 50% / 100% auto no-repeat;
    }
  }

  @include desktop {
    max-width: 905px;
    margin: 0 auto;

    .navigation-wrapper {
      margin: -12px -20px;
    }

    .navigation-item {
      width: calc(100% / 3);
      padding: 12px 20px;
    }

    .item-link {
      font-size: 0.19rem;
      letter-spacing: $letter-spacing-paragraph;
    }
  }

  @include mobile {
    .navigation-wrapper {
      flex-direction: column;
    }

    .navigation-wrapper {
      margin: -0.1rem 0;
    }

    .navigation-item {
      width: 100%;
      padding: 0.1rem 0;
    }

    .item-link {
      font-size: $fontsize-paragraph;
    }
  }
}

.navigation-common-sitemap.is-error {
  @include desktop {
    max-width: 650px;

    .navigation-item {
      width: calc(100% / 2);
    }
  }
}
