@charset "utf-8";

.breadcrumb-common {
  padding: 0.2rem 0;

  .wrapper {
    display: flex;
    justify-content: flex-end;
  }

  .breadcrumb-wrapper {
    display: flex;
    margin: -0.14rem;
    padding: 0;
    list-style: none;
  }

  .breadcrumb-item {
    position: relative;
    padding: 0.1rem 0.14rem;
    letter-spacing: $letter-spacing-reset;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: -0.05rem;
      bottom: 0;
      width: 0.11rem;
      background: url("../img/common/icon_arrow_01.png") 50% 50% / 100% auto no-repeat;
    }

    &:last-child {
      &::before {
        content: none;
      }
    }
  }

  .item-wrapper {
    font-weight: $fontweight-bold;
  }

  @include desktop {
    .breadcrumb-item {
      font-size: 0.15rem;
    }
  }

  @include mobile {
    .breadcrumb-item {
      font-size: $fontsize-paragraph;
    }
  }
}
