@charset "utf-8";

.list-common-bullets {
  .list-wrapper {
    @include reset-list;
  }

  .item-content {
    display: flex;
    letter-spacing: 0.08em;
    line-height: calc(26 / 15);
    font-feature-settings: "palt";
  }

  .list-item {
    font-weight: $fontweight-default;
  }

  @include desktop {
    .list-item {
      margin-right: -23px;
    }

    .item-content {
      font-size: 0.15rem;
    }
  }

  @include mobile {
    .item-content {
      font-size: $fontsize-default-sp;
      line-height: $line-height-sp;
    }
  }
}

.list-common-notes {
  .list-wrapper {
    @include reset-list;
  }

  .list-item {
    color: $color-dark;
    font-weight: $fontweight-default;
    letter-spacing: $letter-spacing-title;
  }

  @include desktop {
    .list-item {
      font-size: 0.12rem;
      line-height: calc(20 / 12);
    }
  }
}

.list-common-flow {
  .list-wrapper {
    @include reset-list;
  }

  .list-item {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      background: url("../img/common/icon_arrow_03.svg") 50% 50% / 100% auto no-repeat;
    }

    &:last-child {
      margin-bottom: 0;
      padding-bottom: 0;

      &::before {
        content: none;
      }
    }
  }

  .item-wrapper {
    display: flex;
    align-items: center;
    background-color: #dae3ea;
  }

  .item-number {
    flex: 0 0 auto;
  }

  .number-content {
    display: block;
    border-radius: 50%;
    background-color: $color-background-title;
  }

  .content-detail {
    color: $color-dark;

    &:last-child {
      .detail-text {
        margin-bottom: 0;
      }
    }
  }

  @include desktop {
    .list-item {
      margin-bottom: 8px;
      padding-bottom: 64px;

      &::before {
        left: calc(50% - 58px);
        width: 66px;
        height: 45px;
      }
    }

    .item-wrapper {
      max-width: 587px;
      margin: 0 auto;
    }

    .number-content {
      width: 101px;
      height: 100px;
      margin-left: -50px;
    }

    .item-content {
      padding: 26px 30px;
    }

    .content-detail {
      font-size: 0.19rem;
      letter-spacing: $letter-spacing-paragraph;
    }

    .title-common-secondary {
      margin-bottom: 1px;
    }
  }

  @include mobile {
    .list-item {
      margin-bottom: 0.2rem;
      padding-bottom: 0.5rem;

      &::before {
        left: calc(50% - 0.16rem);
        width: 0.46rem;
        height: 0.25rem;
      }
    }

    .number-content {
      width: 0.61rem;
      height: 0.6rem;
      margin-left: -0.25rem;
    }

    .item-wrapper {
      margin-left: 0.2rem;
    }

    .item-content {
      padding: 0.2rem 0.1rem;
    }

    .title-common-secondary {
      margin-bottom: 0.1rem;
    }
  }
}

.list-common-faq {
  .list-wrapper {
    @include reset-list;
  }

  .category-text {
    display: flex;
    position: relative;
    margin-bottom: 0;
    padding: 0.08rem 0.24rem;
    color: $color-dark;
    font-weight: $fontweight-bold;
    letter-spacing: $letter-spacing-paragraph;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      background-color: $color-dark;
    }
  }

  @include desktop {
    .item-category {
      padding-left: 13px;
    }

    .category-text {
      font-size: 0.28rem;

      &::before {
        top: 20px;
        width: 21px;
        height: 21px;
      }
    }

    .list-item {
      margin-bottom: 44px;
    }
  }

  @include mobile {
    .item-category {
      padding-left: 0.1rem;
    }

    .category-text {
      font-size: 0.24rem;

      &::before {
        top: 0.17rem;
        width: 0.2rem;
        height: 0.2rem;
      }
    }

    .list-item {
      margin-bottom: 0.4rem;
    }
  }
}
