@charset "utf-8";

.keyvisual-common {
  .keyvisual-title {
    margin-bottom: 0;
    color: $color-background-title;
    font-weight: $fontweight-bold;
    text-align: center;
    text-shadow: 0.05rem 0 0 rgba(255, 255, 255, 1);

    span {
      background: linear-gradient(#fff20e, #fff20e) no-repeat;
      background-position: left bottom;
    }
  }

  @include desktop {
    min-height: 412px;
    padding: 158px 0;
    background: url("../img/common/background_keyvisual_01.jpg") 50% 50% / 100% 100% no-repeat;

    .keyvisual-title {
      font-size: 0.55rem;

      span {
        padding: 6px 0;
        background-size: 100% 35px;
      }
    }
  }

  @include mobile {
    min-height: 3rem;
    padding: 1.12rem 0;
    background: url("../img/common/sp/background_keyvisual_01.jpg") 50% 50% / 100% auto no-repeat;

    .keyvisual-title {
      margin-right: -0.08rem;
      font-size: 0.45rem;

      span {
        padding: 0.02rem 0;
        background-size: 100% 0.3rem;
      }
    }
  }
}

.keyvisual-common.is-inquiry {
  @include mobile {
    .wrapper {
      padding-right: 0.13rem;
      padding-left: 0.13rem;
    }

    .keyvisual-title {
      margin-right: 0;
    }
  }
}
